.bb8 {
  position: absolute;
  margin-left: calc(-140px / 2);
  width: 140px;
  bottom: 15%;
  left: 0;

}

.antennas {
  position: absolute;
  transition: left 0.6s;
  left: 28%;
}

.antennas.right {
  left: 6%;
}

.antenna {
  background: #e0d2be;
  position: absolute;
  width: 2px;
}

.antenna.short {
  height: 20px;
  top: -65px;
  left: 50px;
}

.antenna.long {
  border-top: 6px solid #020204;
  border-bottom: 6px solid #020204;
  height: 36px;
  top: -80px;
  left: 56px;
}

.head {
  background-color: ghostwhite;
  border-radius: 90px 90px 25px 25px;
  -moz-border-radius: 90px 90px 25px 25px;
  -webkit-border-radius: 90px 90px 25px 25px;
  height: 63px;
  margin-left: -45px;
  overflow: hidden;
  position: absolute;
  width: 104px;
  z-index: 3;
  top: -56px;
  left: 53%;
}

.stripe {
  position: absolute;
  width: 100%;
}

.stripe.one {
  background: #7699B7;
  height: 7px;
  opacity: 0.8;
  z-index: 1;
  top: 3px;
}

.stripe.two {
  background: #CD7640;
  height: 4px;
  top: 14px;
}

.stripe.three {
  background: #999;
  height: 4px;
  opacity: 0.5;
  bottom: 3px;
}

.stripe.detail {
  display: flex;
  width: 200px;
  bottom: 7px;
  left: -38%;
  transition: left 0.6s;
}

.stripe.detail.right {
  left: 0;
}

.detail {
  height: 7px;
}

.detail.zero {
  background-color: #CD7640;
  width: 2%;
  margin-left: 3px;
}

.detail.one {
  background-color: #CD7640;
  width: 8%;
  margin-left: 3px;
}

.detail.two {
  background-color: #CD7640;
  width: 6%;
  margin-left: 5px;
}

.detail.three {
  background-color: #CD7640;
  width: 4%;
  margin-left: 45px;
  height: 5px;
  margin-top: 2px;
}

.detail.four {
  background-color: #CD7640;
  width: 10%;
  margin-left: 4px;
}

.detail.five {
  background-color: #CD7640;
  width: 2%;
  margin-left: 3px;
}

.eyes {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  transition: left 0.6s;
  left: 0;
}

.eyes.right {
  left: 36%;
}

.eye {
  border-radius: 50%;
  display: block;
  position: absolute;
}

.eye.one {
  background: #020204;
  border: 4px solid lightgray;
  height: 30px;
  width: 30px;
  top: 12px;
  left: 12%;
}

.eye.one:after {
  background: white;
  border-radius: 50%;
  content: "";
  display: block;
  height: 3px;
  position: absolute;
  width: 3px;
  top: 4px;
  right: 4px;
}

.eye.two {
  background-color: lightgrey;
  border: 1px solid #020204;
  height: 16px;
  width: 16px;
  top: 30px;
  left: 40%;
}

.eye.two:after {
  background: #020204;
  border-radius: 50%;
  content: "";
  display: block;
  height: 10px;
  position: absolute;
  width: 10px;
  top: 2px;
  left: 2px;
}

.ball {
  background-color: ghostwhite;
  border-radius: 50%;
  height: calc(140px + 25px);
  overflow: hidden;
  position: relative;
  width: calc(140px + 25px);
  z-index: 2;
}

.lines {
  border: 2px solid #B19669;
  border-radius: 50%;
  height: 400px;
  opacity: 0.6;
  position: absolute;
  width: 400px;
}

.lines.two {
  top: -10px;
  left: -250px;
}

.ring {
  background: #CD7640;
  border-radius: 50%;
  height: 70px;
  margin-left: -35px;
  position: absolute;
  width: 70px;

}

.ring.one {
  margin-left: -40px;
  height: 90px;
  width: 100px;
  top: 2%;
  left: 42%;
}

.ring.two {
  height: 40px;
  width: 80px;
  transform: rotate(50deg);
  top: 65%;
  left: 8%;
}

.ring.two:after {
  top: 100%;
}

.ring.three {
  height: 37px;
  width: 80px;
  transform: rotate(-50deg);
  top: 68%;
  left: 84%;
}

.ring.three:after {
  top: 110%;
}

.shadow {
  background: #3a271c;
  box-shadow: 5px 0 50px #3a271c;
  border-radius: 50%;
  height: calc(140px / 6);
  opacity: 0.80;
  position: absolute;
  width: 140px;
  z-index: 1;
  left: 10px;
  bottom: -8px;
}
